import React, { useState } from "react";
import { Gallery } from "react-grid-gallery";
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css";

const BlogComponent = () => {
  // Static images data
  const images = [
    {
        src: "https://c2.staticflickr.com/9/8817/28973449265_07e3aa5d2e_b.jpg",
        original: "https://c2.staticflickr.com/9/8817/28973449265_07e3aa5d2e_b.jpg",
        width: 320,
        height: 174,
        tags: [
          { value: "Nature", title: "Nature" },
          { value: "Flora", title: "Flora" },
        ],
        caption: "After Rain (Jeshu John - designerspics.com)",
      },
      {
        src: "https://c7.staticflickr.com/9/8546/28354329294_bb45ba31fa_b.jpg",
        original: "https://c7.staticflickr.com/9/8546/28354329294_bb45ba31fa_b.jpg",
        width: 320,
        height: 213,
        caption: "Red Apples with other Red Fruit (foodiesfeed.com)",
      },
  ];

  const [index, setIndex] = useState(-1);

  const isValidIndex = (i) => i >= 0 && i < images.length;

  if (images.length === 0) {
    return <div>No images available</div>;
  }

  const currentImage = isValidIndex(index) ? images[index] : null;
  const nextIndex = isValidIndex(index + 1) ? index + 1 : 0;
  const nextImage = isValidIndex(nextIndex) ? images[nextIndex] : currentImage;
  const prevIndex = isValidIndex(index - 1) ? index - 1 : images.length - 1;
  const prevImage = isValidIndex(prevIndex) ? images[prevIndex] : currentImage;

  const handleClick = (i) => setIndex(i);
  const handleClose = () => setIndex(-1);
  const handleMovePrev = () => setIndex(prevIndex);
  const handleMoveNext = () => setIndex(nextIndex);

  return (
    <div>
      <Gallery
        images={images}
        onClick={handleClick}
        enableImageSelection={false}
      />
      {!!currentImage && (
        <Lightbox
          mainSrc={currentImage.original}
          imageTitle={currentImage.caption}
          mainSrcThumbnail={currentImage.src}
          nextSrc={nextImage.original}
          nextSrcThumbnail={nextImage.src}
          prevSrc={prevImage.original}
          prevSrcThumbnail={prevImage.src}
          onCloseRequest={handleClose}
          onMovePrevRequest={handleMovePrev}
          onMoveNextRequest={handleMoveNext}
        />
      )}
    </div>
  );
};

export default BlogComponent;
